import React from 'react';
import { Element, useNode, useEditor } from "@craftjs/core";

export const Column = ({ children, className, ...props }) => {
  const {
    connectors: { connect },
  } = useNode();

  return (
    <div {...props} ref={connect} className={'col ' + className}>
      {children ? <React.Fragment>{children}</React.Fragment> : <div className='div-empty'>Empty Col</div>}
    </div>
  );
};

export default function MyCol({ children, ...props }) {
  const { enabled } = useEditor((state) => ({
    enabled: state.options.enabled,
  }));

  
      return (<>
        {children ??
        [...Array(1).keys()].map((id) => (
            <Element
              is={Column}
              id={'col-' + Math.floor(Math.random() * 1000) + 1} 
              canvas
              className={'col'}
            
            />
            ))
          }
          </>
      );
}


